import axios from 'axios';
import * as constants from './constants';

const changeComment = (result) => ({
	type: constants.CHANGECOMMENT,
	value: result.comment
})

export const buyBook=(buid)=>{
	return (dispatch)=>{
		axios({
			url:"/purchase",
			method:"post",
			data:buid
		}).then(function(response){
				const result=response.data.msg;
				if (result==="SUCCESS") {
					console.log("成功")
				
				}else {
					alert('获取信息失败')
				}
		})
	} 
}
export const getComment = (bookid) => {
	return (dispatch) => {
		axios.post('/bookComment?bookid=' + bookid).then((res) => {

			const data = res.data.data;
			const result=res.data.msg;
			if (result==="SUCCESS") {
				dispatch(changeComment(data));
			
			}else {
				alert('获取信息失败')
			}
		})
	}
}