import React,{Component} from 'react';
import Button from '../../../components/Button/index';
import { connect } from 'react-redux';
import { actionCreators } from '../store/index'
import {
    BookDetailWrapper
}from '../style'
class BookDetail extends  Component{
    handleBuyBook(){
        if(this.props.loginState){
            let phone=this.state.userInfo.get("phone")
            let data={"phone":phone,"bid":this.props.book.bid};
            console.log(data)
        }

    }
    render(){
        const {book}=this.props;
        return(
            <BookDetailWrapper>
                    <span className="title">{book.name}</span>
                    <p className="detail">{book.detailInfo}</p>
                    <div className="writerMess">
                        <span className="writer">作者:{book.writer }</span>
                        <span className="publisher">出版社:{book.publisher }</span>
                    </div>
                    <div className="price">
                        <span >￥{book.price }</span>
                    </div>
                    <Button className="primary four" style={{float:'left'}} onClick={this.handleBuyBook.bind(this)}>立即购买</Button>
                </BookDetailWrapper>
        )
    }

}
const mapTostate=(state)=>({
    loginState:state.getIn(["common","login"]),
    userInfo:state.getIn(["common","userInfo"])

})
const mapToDispatch=(dispatch)=>({
    buyBook(buid){
        dispatch(actionCreators.buyBook(buid))

    }
})
export default connect(mapTostate,mapToDispatch)(BookDetail);